/* Main container */
.full-width-container {
  width: 100%;
  padding: 20px;
  font-family: 'Raleway', sans-serif;
  background-color: #ffffff05;
  color: #fff;
  box-sizing: border-box;
}

/* Search Input */
input[type='text'] {
  width: 30%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
  outline: none;
  box-sizing: border-box;
  position: center;
  margin-left: 35px;
  
}

/* Search Results Header */
h2 {
  font-family: 'Bebas Neue', sans-serif;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 32px;
  color: #333;
  margin-left: 800px;
  margin-top: 20px;
  font-size: 50px;
}

h3{
  margin-left: 36px;
  margin-top: 20px;
  font-family: 'Bebas Neue', sans-serif ;
  margin-bottom: -70px;
}

/* Loading Spinner */
.loading {
  font-size: 18px;
  color: #fff;
}

/* MovieGrid styling */
.movie-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

/* GenreSection styling */
.genre-section {
  margin-top: 40px;
}

.genre-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
  margin-top: -10px;
  margin-bottom: -55px;
  text-align: center;
  text-transform: uppercase;
}

/* GenreCard */
.genre-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.genre-card:hover {
  transform: translateY(-5px);
}

.genre-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.genre-card h3 {
  font-size: 18px;
  font-weight: bold;
  padding: 15px;
  color: #444;
  background-color: #f4f4f4;
  margin: 0;
}

/* For each movie poster on hover */
.movie-card:hover {
  transform: scale(1.05);
}

/* Card overlay (for Like and Download buttons) */
.overlay-btn {
  position: absolute;
  bottom: 15px;
  left: 15px;
  background: rgba(0, 0, 0, 0.6);
  border: none;
  color: white;
  font-size: 18px;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.overlay-btn:hover {
  background: rgba(0, 0, 0, 0.8);
}

.overlay-btn + .overlay-btn {
  left: auto;
  right: 15px;
}

/* Movie details */
.movie-details {
  padding: 20px;
  background-color: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.movie-details img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
}

.movie-details h1 {
  font-size: 28px;
  color: #333;
  margin-top: 20px;
}

.movie-details .like-btn {
  background-color: #ff4757;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

.loading-genres{
  margin-left: 38px;

}
.loading{
  margin-left: 38px;
  color:#333

}

.page-title{
  margin-left: 730px;

}

.movie-details .like-btn:hover {
  background-color: #ff6b81;
}

.movie-details .download-btn {
  background-color: #2ed573;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

.movie-details .download-btn:hover {
  background-color: #55efc4;
}